.pricing {
  .pricing-title {
    margin-bottom: 10px; }
  .pricing-desc {
    position: relative;
    margin-bottom: 65px;
    &::after {
      @include title_line; } }

  .pricing-items {
    @include flex-align-center(v, row wrap);
    article {
      flex: 1 0 33%;
      @include flex-align-center(both, column wrap);
      margin-bottom: 10px;

      [class*="-img"] {
        @include flex-align-center;
        transform: scale(0.85);
        margin-bottom: 35px;
        width: 100%;
        height: 240px; }

      [class*="-title"], [class*="-desc"] {
        margin-bottom: 15px; }

      [class*="-desc"] {
        width: 80%;
        text-align: center; }

      [class*="-price"] {
        position: relative;
        margin-bottom: 25px;
        .dollar, .cost, .per {
          font-family: $mont;
          font-weight: 400; }
        .dollar, .per {
          font-size: 20px;
          color: $basic-color; }
        .dollar {
          position: absolute;
          top: 0.5em;
          left: -1em; }
        .cost {
          font-size: 50px; } }

      [class*="-link"] {
        @include link-btn; } } } }

.md, .xs {
  .pricing {
    .pricing-desc {
      margin-bottom: 50px; }
    .pricing-items {
      article {
        [class*="-img"] {
          height: 252px;
          margin-bottom: 15px;
          img {
            max-width: 200px; } } } } } }

.xs {
  .pricing {
    .pricing-items {
      flex-direction: column;
      article {
        flex: initial;
        [class*="-img"] {
          height: auto;
          img {
            max-width: 257px; } }

        [class*="-desc"] {
          padding: 0;
          margin-bottom: 20px; }

        [class*="-link"] {
          margin-bottom: 20px; } } } } }
