h1, h2, h3 {
  font-family: $mont;
  font-weight: 900;
  text-transform: uppercase; }
h1 {
  font-size: 45px;
  line-height: 45px;
  .md &,.xs & {
    font-size: 35px;
    line-height: 1; } }
h2 {
  font-size: 30px;
  .md &,.xs & {
    font-size: 24px;
    text-align: center; } }

p, span {
  &[class*="description"], &[class*="desc"] {
    font-family: $open-sans;
    font-weight: 300;
    font-size: 14px;
    color: $gray;
    .md &,.xs & {
      font-size: 13px;
      text-align: center; } } }

p, span {
  &[class*="name"], &[class*="title"] {
    font-family: $mont;
    font-weight: 300;
    font-size: 20px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    .md &,.xs & {
      font-size: 18px;
      text-align: center; } } }

.testimonials, .features, .projects, .pricing, .about-us, .contacts, .home {
  position: relative;
  @include flex-align-center(both, row wrap);
  padding: 50px 30px;
  // min-height: 90vh
  width: 100%;
  color: #fff; }

.home, .features, .pricing {
  background: $black; }

.testimonials, .projects, .about-us, .contacts {
  background: #fff;
  color: #000; }

.home {
  min-height: 100vh;
  padding-top: 140px;
  padding-bottom: 80px;
  .xs & {
    padding-top: 80px; } }

.testimonials, .features, .projects, .pricing, .about-us, .contacts {
  flex-flow: column; }

.about-us, .contacts {
  padding-right: 0;
  padding-left: 0; }

