.projects {
  flex-direction: column;
  justify-content: flex-start;
  .projects-title {
    margin-bottom: 10px; }
  .projects-desc {
    position: relative;
    margin-bottom: 65px;
    &::after {
      @include title_line; } }

  .btn-group {
    margin-bottom: 30px;
    button {
      margin-right: 30px;
      padding-bottom: 10px;
      font-family: $mont;
      font-weight: 300;
      font-size: 12px;
      color: $basic-color;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      border-bottom: 1px solid transparent;
      cursor: pointer;
      &.active {
        color: $black;
        border-bottom: 1px solid $basic-color;
        outline: none; } }
    button:last-child {
      margin-right: 0; } }
  .items-group {
    width: 980px; }
  .element-item {
    position: relative;
    float: left;
    width: 230px;
    height: 230px;
    margin-left: 10px;
    margin-bottom: 10px;
    font-size: 14px;
    perspective: 600px;
    transition: all 0.5s linear;
    &.hover {
      transform: translateY(-3px); }
    &.hover .card, &.hover .card {
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
      @include animation(flip-in 1s forwards 0.5s); }
    .non-touch &:hover, .non-touch  &:focus {
      transform: translateY(-3px); }
    .non-touch &:hover .card, .non-touch &:focus .card {
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
      @include animation(flip-in 1s forwards 0.5s); }

    .card {
      width: 100%;
      height: 100%;
      position: absolute;
      transform-style: preserve-3d;
      border-radius: 13px;
      .front, .back {
        margin: 0;
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        overflow: hidden;
        border-radius: 13px;
        span {
          display: block; } }
      .front {
        top: 0;
        left: 0;
        z-index: 2;
        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate( -50%, -50% );
          max-width: 100%;
          height: 100%; } }

      .back {
        padding: 20px;
        color: #000;
        background: $basic-color;
        transform: rotateY( 180deg );
        .item-title, .item-desc {
          text-align: left; }
        .item-title {
          padding-bottom: 10px;
          font-size: 16px;
          font-weight: 700;
          letter-spacing: normal;
          color: #fff; }
        .item-desc {
          padding-bottom: 20px;
          font-size: 12px;
          color: #fff; }
        .item-link {
          @include link-btn( null, 10px 20px 10px, #fff, $black);
          width: 100px;
          border: 2px solid #fff; }
        .close-btn {
          display: none;
          position: absolute;
          top: 10px;
          right: 15px;
          font-size: 20px;
          color: #FFF; } } } } }



.md {
  .items-group {
    width: 730px; } }
.xs {
  .projects {
    .items-group {
      width: 100%; }
    .element-item {
      width: 100%;
      margin-left: 0;
      .card {
        .front img {
          width: auto;
          height: auto; }
        .back {
          .close-btn {
            display: block; } } } } } }


