.about-us {
  .about-us-title {
    margin-bottom: 10px; }
  .about-us-desc {
    position: relative;
    margin-bottom: 65px;
    &::after {
      @include title_line; } }

  .social-items {
    display: flex;
    align-items: flex-start;
    flex-flow: row wrap;
    margin-bottom: 300px;
    [class*="item-"] {
      display: flex;
      align-items: flex-start;
      margin-left: 30px;
      margin-right: 30px;
      .img-wrapper {
        margin-top: 8px;
        width: 28px;
        height: auto;
        img {
          width: 100%; } }
      .figure-wrapper {
        margin-left: 5px;
        .count {
          font-family: $mont;
          font-weight: 300;
          font-size: 33px; }
        .unit {
          font-family: $open-sans;
          font-weight: 300;
          font-size: 13px;
          color: $gray; } } } }

  overflow: hidden;
  .insta-items {
    position: absolute;
    bottom: 0;
    @include flex-align-center(v);
    overflow: hidden;
    img {
      vertical-align: bottom; }
    .insta-follow {
      @include flex-align-center(both, column wrap);
      width: 300px;
      height: 300px;
      background: #191919;
      .insta-title {
        margin-bottom: 15px;
        font-size: 10px;
        font-weight: 300;
        color: #fff; }
      .insta-logo :first-child {
          margin-bottom: 10px; }
      .insta-desc {
        margin: 15px 15px 25px;
        font-size: 12px;
        text-align: center; }
      .insta-link {
        @include link-btn(#101010); } } } }

.xs {
  .about-us {
    padding-bottom: 0;
    .social-items {
      margin-bottom: 15px;
      [class*="item-"] {
        flex: 1 0 30%;
        margin-bottom: 20px;
        .figure-wrapper {
          @include flex-align-center(v);
          .unit {
            align-self: flex-end;
            margin-left: 5px;
            margin-bottom: 5px;
            br {
              display: none; } } } } }

    .insta-items {
      position: static;
      flex-wrap: wrap;
      .insta-item02, .insta-item03 {
        width: 50%;
        img {
          width: 100%; } }
      .insta-item01, .insta-item04, .insta-item05, .insta-item06 {
          display: none; }

      .insta-follow {
        order: 10;
        width: 100%;
        height: 252px; } } } }


.subscribe {
  @include flex-align-center(both, column wrap);
  padding: 50px 30px;
  background: $black;
  color: #fff;
  .subscribe-form {
    @include flex-align-center(both, column wrap);
    width: 100%;
    margin-top: 50px;
    .subscribe-desc {
      margin-bottom: 35px;
      font-family: $play;
      font-size: 17px;
      color: $basic-color;
      letter-spacing: 0.2em;
      text-transform: lowercase; }
    .subscribe-link {
      @include link-btn(#101010); }
    form {
      @include flex-align-center(v, row wrap);
      width: 100%;

      margin-bottom: 50px;
      [class*="sub-"] {
        flex: 1 0 30%;
        position: relative;
        &:nth-child(1) {
          padding-right: 15px; }
        &:nth-child(2) {
          padding-left: 15px;
          padding-right: 15px; }
        &:nth-child(3) {
          padding-left: 15px; }
        label {
          margin-bottom: 10px;
          font-family: $mont;
          font-size: 12px;
          color: #515151;
          letter-spacing: 0.2em;
          text-transform: uppercase;
          &::after {
            content: ' ●';
            color: $basic-color; } }
        .ngInput {
          @include input-box($dark-gray); }
        .valid-msg {
          position: absolute;
          bottom: 0;
          left: 25px;
          font-family: $open-sans;
          font-size: 11px;
          color: $red; }
        .ng-touched.ng-invalid {
          border: 2px solid $red; } } } } }

.xs {
  .subscribe {
    .subscribe-img img {
      width: 100%; }
    .subscribe-form form {
      [class*="sub-"] {
        flex: 1 0 100%;
        padding: 0; } } } }
