// Color
$basic-color: #ac9455;
$black: #141414;
$font-black: #2b2b2b;
$gray: #a3a3a3;
$dark-gray: #212121;
$nav-black: #010101;
$xs-nav-black: rgba(20, 20, 20, 0.95);
$red: #ef5151;
// Font
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
@import url(https://brick.a.ssl.fastly.net/Montserrat:300,400,700,900);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400i);

$open-sans: "Open Sans", sans-serif;
$mont: "Montserrat", sans-serif;
$play: "Playfair Display", serif;

// nav icon
@mixin navicon_lines {
  display: inline-block;
  width: 15px;
  height: 1px;
  background: $basic-color;
  border-radius: 1px;
  transition: .3s ease; }

@mixin title_line {
  content: '';
  position: absolute;
  bottom: -25px;
  left: 50%;
  transform: translate(-50%);
  display: block;
  width: 65px;
  border-top: 1px solid $basic-color; }

@mixin link-btn($bg:null, $size: 10px 40px 10px, $hover-color: $basic-color, $hover-font: null) {
  @include flex-align-center;
  font-family: $mont;
  font-size: 12px;
  text-transform: uppercase;
  padding: $size;
  letter-spacing: 0.2em;
  color: #fff;
  text-decoration: none;
  border-radius: 30px;
  border: 2px solid $basic-color;
  background-color: $bg;
  &:hover {
    background-color: $hover-color;
    color: $hover-font; } }

@mixin input-box($color:null, $size: 10px 40px 10px ) {
  display: flex;
  align-items: flex-start;
  width: 100%;
  font-family: $open-sans;
  font-size: 12px;
  text-transform: capitalize;
  padding: $size;
  color: $gray;
  text-decoration: none;
  border-radius: 30px;
  border: 2px solid $color;
  background-color: $color;
  &:focus {
    border: 2px solid $basic-color;
    background-color: #fff;
    outline: none; } }


@mixin keyframes($animation) {
  @keyframes #{$animation} {
    @content; } }

@mixin animation($str) {
  animation: $str; }


@include keyframes(flip-in) {
  100% {
    transform: rotateY( 180deg ); } }
