.contacts {
  .contacts-title {
    margin-bottom: 10px; }
  .contacts-desc {
    position: relative;
    margin-bottom: 65px;
    &::after {
      @include title_line; } }
  .contacts-form {
    @include flex-align-center(both, column wrap);
    width: 80%;
    .contacts-link {
      @include link-btn;
      &:hover {
        color: #fff; }
      color: $black; }
    form {
      @include flex-align-center(v, row wrap);
      width: 100%;
      margin-bottom: 50px;
      [class*="contacts-"] {
        flex: 1 0 50%;
        position: relative;
        &:nth-child(1) {
          padding-right: 15px; }
        &:nth-child(2) {
          padding-left: 15px; }
        label {
          margin-bottom: 10px;
          font-family: $mont;
          font-size: 12px;
          color: #515151;
          letter-spacing: 0.2em;
          text-transform: uppercase;
          &:not(.msg)::after {
            content: ' ●';
            color: $basic-color; } }
        .ngInput {
          @include input-box(#efefef);
          &.msg {
            width: 100%; } }
        textarea {
          padding: 5px;
          width: 100%;
          height: 120px;
          border: 2px solid #efefef;
          border-radius: 10px;
          background: #efefef; }

        .valid-msg:not(.msg) {
          position: absolute;
          bottom: 0;
          left: 25px;
          font-family: $open-sans;
          font-size: 11px;
          color: $red; }
        .ng-touched.ng-invalid:not(.msg) {
          border: 2px solid $red; } } } } }

.map, .gmap_canvas {
  height: 225px;
  width: 100%;
  overflow: hidden;
  background: #ae9758; }

.contacts-info {
  @include flex-align-center(v, row wrap);
  justify-content: space-between;
  padding: 50px;
  .office-info {
    @include flex-align-center(v, row wrap);
    .phone-number, .office-address, .mail-address {
      @include flex-align-center(v, row wrap);
      margin-right: 30px;
      padding: 5px 0;
      font-family: $open-sans;
      font-size: 14px;
      .icon {
        @include flex-align-center;
        margin-right: 10px;
        width: 18px;
        height: auto; } }

    .phone-number img {
      width: 18px;
      height: 18px; }
    .office-address img {
      width: 8px;
      height: 18px; }
    .mail-address img {
      width: 18px;
      height: 13px; } }
  .social-info {
    padding: 20px 0;
    a img {
      margin-left: 25px;
      width: auto;
      max-height: 15px; } } }

.xs, .md {
  .contacts {
    .contacts-desc {
      padding-right: 30px;
      padding-left: 30px; }
    .contacts-info {
      span {
        font-size: 13px; } }
    .contacts-form {
      padding: 30px;
      width: 100%;
      form {
        [class*="contacts-"] {
          flex: 1 0 100%;
          padding: 0; } } } } }

.xs {
  .contacts-info {
    padding: 30px 20px;
    flex-direction: column;
    align-items: flex-start;
    .office-info {
      flex-direction: column;
      align-items: flex-start; }
    .social-info :first-child img {
        margin-left: 0; } } }

